@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;900&family=Roboto:ital,wght@0,300;0,400;0,500;1,300&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root{
  --poppins: 'Poppins', sans-serif;;
  --roboto: 'Roboto', sans-serif;
}

body{
  height: 100vh;
  width: 100%;
}

nav{
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  align-items: center;
}

.logo{
  font-family: var(--roboto);
  font-size: 1rem;
  font-weight: 400;
}

.logo span{
  color: dodgerblue;
  font-weight: 500;
  font-size: 1.1rem;
}

.container{
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: .5rem;
  gap: .5rem;
  padding: 10px;
  
}

.search_container{
  grid-row-start: 1;
  grid-row-end: 3;
}

li.meaningLi::marker{
  font-family: var(--roboto);
  margin-right: 1rem;
  font-size: 14px;
  display: flex;
}

h1, h2{
  font-size: 2rem;
  font-family: var(--roboto);
  color: dodgerblue;
  margin-bottom: 14px;
}

.information{
  display: flex;
  justify-content: space-between;
}

h2.about_h2{
  font-size: 1.5rem;
  font-weight: 400;
  font-family: var(--roboto);
}

p.about_p{
  font-family: var(--poppins);
  color: grey;
}

article{
  margin-bottom: 1.2rem;
}

@media screen and (max-width: 900px){
  html{
    font-size: 14px;
  }

  .container{
    grid-template-columns: 1fr;
  }
  .search_container{
    grid-row-start: 1;
    grid-row-end: 2;
  }
}

@media screen and (max-width: 600px){
  .search_container{
    grid-row-start: 2;
    grid-row-end: 3;
  }
  .word-of-the-day_container{
    grid-row-start: 1;
    grid-row-end: 2;
  }
  nav{
    padding-top: 10px;
    padding-bottom: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
